import React from "react";
import { TiHome } from "react-icons/ti";
import styled from "styled-components";
import { useAuth } from "../Context/Auth";
import { FaUserCog } from "react-icons/fa";
import { NavLink, useNavigate, useParams } from "react-router-dom";
import { MdClose } from "react-icons/md";
import { useCookies } from "react-cookie";
import { HiPlus } from "react-icons/hi";

const SideNavigationBar = () => {
  const auth = useAuth();
  const param = useParams();
  const navigate = useNavigate();
  // eslint-disable-next-line no-unused-vars
  const [cookie, setCookie, removeCookie] = useCookies(["log"]);

  React.useEffect(() => {
  }, []);

  const handleLogout = () => {
    removeCookie("log", { path: "/" });
    auth.logout();
    navigate("/", { replace: true });
  };

  return (
    <Nav>
      <Container>
        <Brand>
          <Image
            src={require("../components/images/BOU logo.png")}
            alt="logo"
          />
          <BrandPanel>
            <BrandText>বাংলাদেশ উন্মুক্ত বিশ্ববিদ্যালয়</BrandText>
            <BrandText>Bangladesh Open University</BrandText>
          </BrandPanel>
        </Brand>
        <BarIcon>
          <MdClose />
        </BarIcon>
      </Container>
      <List>
        <Link to={`/t/${param.tid}/dashboard`}>
          <Icon
            onClick={() => {
              auth.handleSetTitle("Class Assesment Marks");
            }}
          >
            <TiHome />
            <Text>Dashboard</Text>
          </Icon>
        </Link>
        <Link to={`/t/${param.tid}/class/create`}>
          <Icon>
            <HiPlus />
            <Text>Create</Text>
          </Icon>
        </Link>
        <BottomTools>
          <Link to={`/t/${param.tid}/profile`}>
            <Icon>
              <FaUserCog />
              <Text>Profile</Text>
            </Icon>
          </Link>
          <Icon onClick={handleLogout}>
            <Image
              // onClick={handleLogout}
              style={{ width: "27px", height: "27px", padding: "0" }}
              src={auth.user.image || require("../components/images/man.png")}
              alt="user"
            />
            <Text>Logout</Text>
          </Icon>
        </BottomTools>
      </List>
    </Nav>
  );
};

export default SideNavigationBar;

const Nav = styled.nav`
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  padding: 0.5rem 0.1rem;
  background: blue;
  background: #2f3542;
  height: 100vh;
  width: 100%;
  margin: auto 0;
  padding-top: 1rem;
  filter: drop-shadow(3px 0px rgba(3, 3, 3, 0.2));
  @media (max-width: 820px) {
    width: 100vw;
    padding-left: 3rem;
  }
  @media (max-width: 600px) {
    width: 100vw;
    padding-left: 2rem;
  }
`;

const Container = styled.div`
  display: flex;
  flex-wrap: nowrap;
  margin-bottom: 2rem;
`;
const BarIcon = styled.div`
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 3px;
  color: #fff;
  display: none;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
  cursor: pointer;
  position: relative;
  margin: 0 10px 0 auto;
  &:hover {
    background: red;
    border-color: red;
  }
  @media (max-width: 820px) {
    display: flex;
  }
`;

const List = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0;
  padding-left: 0.5rem;
  height: 82%;
  width: 100%;
`;

const Icon = styled.div`
  width: 100%;
  height: 35px;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  padding: 0.2rem 0.4rem;
  cursor: pointer;

  &:hover {
    background: #fff;
    border-radius: 5px;
  }
`;

const Text = styled.p`
  margin-bottom: 0;
  font-size: 1rem;
  margin-left: 0.5rem;
  font-weight: 500;
  display: none;
  &:hover {
    color: blue;
  }
  @media (max-width: 820px) {
    display: block;
  }
`;

const Image = styled.img`
  width: 40px;
  height: 40px;
  padding: 0.2rem 0.3rem;
  border-radius: 5px;
  background: rgba(241, 242, 246, 1);
`;
const Brand = styled.div`
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  gap: 0 0.4rem;
  align-items: center;
  margin-bottom: 2rem;
  margin-left: 0.5rem;
  @media (max-width: 820px) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
`;
const BrandPanel = styled.div`
  display: none;

  @media (max-width: 820px) {
    display: flex;
    flex-direction: column;
    margin-top: 0.5rem;
    margin-top: 0;
  }
`;

const BrandText = styled.h1`
  font-size: 0.6rem;
  font-weight: 600;
  margin-bottom: 0;
  @media (max-width: 820px) {
    font-size: 0.9rem;
  }
  @media (max-width: 400px) {
    font-size: 0.8rem;
  }
`;

const Link = styled(NavLink)`
  text-decoration: none;
  color: #fff;
  font-size: 1.5rem;
  height: 40px;
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 0.5rem;
`;

const BottomTools = styled.div`
  display: block;
  margin: auto 0 1rem 0;
`;
